import React from "react";
import s from './Dialogs.module.css';
import DialogItem from "./DialogItem/DialogItem";
import Message from "./Message/Message";
import {Field, reduxForm} from "redux-form";
import {Textarea} from "../common/FormsControls/FormsControls";
import {maxLengthCreator, required} from "../../utils/validators/validators";

const Dialogs = (props) => {

    let dialogElements = props.dialogsPage.dialogs.map((user) =>
        <DialogItem name={user.name} key={user.id} id={user.id}/>);

    let massagesElements = props.dialogsPage.messages.map((m) =>
        <Message message={m.message} key={m.id}/>);


    let addNewMessage = (value) => {
        let text = value.newMessageBody;
        props.sendMessage(text);
    }

    return (
        <div className={s.dialogs}>
            <div className={s.dialogsItems}>
                {dialogElements}
            </div>
            <div className={s.messages}>
                <div>{massagesElements}</div>
                <AddMessageFormRedux onSubmit={addNewMessage}/>
            </div>
        </div>
    );
}

const maxLength50 = maxLengthCreator(50);

const AddMessageForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit}>

            <div><Field component={Textarea}
                        name="newMessageBody"
                        placeholder="Enter your message"
                        validate={[required,maxLength50]}/></div>
            <div>
                <button>Send</button>
            </div>
        </form>
    )

}
const AddMessageFormRedux = reduxForm({form: "dialogMessageForm"})(AddMessageForm)


export default Dialogs;